
import React, { Component } from 'react'
import { Link, NavLink } from 'react-router-dom';

class Navbar extends Component {
  render() {
    return (
      <section className="bg-black-25 mb-5">
        <div className="container-fluid">
          <nav className="navbar navbar-expand-lg navbar-dark bg-transparent px-0 py-3">

            <Link className="navbar-brand text-warning" to="/home"><strong><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/24/ffc107/film-reel--v1.png" alt="logo"/>MyMDB_</strong></Link>

            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">

              <ul className="navbar-nav mr-auto">
                <li className="nav-item ml-3">
                  <NavLink className="nav-link" to="/home"><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/22/ffffff/home-page.png" alt="home"/>Home</NavLink>
                </li>
                <li className="nav-item ml-3">
                  <NavLink className="nav-link" to="/movies"><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/22/ffffff/cinema---v1.png" alt="movies"/>Movies</NavLink>
                </li>
                <li className="nav-item ml-3">
                  <NavLink className="nav-link" to="/tv"><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/22/ffffff/retro-tv.png" alt="tv shows"/>TV Shows</NavLink>
                </li>
                <li className="nav-item ml-3">
                  <NavLink className="nav-link" to="/people"><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/22/ffffff/group.png" alt="people"/>People</NavLink>
                </li>
              </ul>

              <ul className="navbar-nav ml-auto">
                <li className="nav-item mr-3">
                  <NavLink className="nav-link" to="/login">Login</NavLink>
                </li>
                <li className="nav-item mr-3">
                  <NavLink className="nav-link" to="/register">Register</NavLink>
                </li>
              </ul>

              <div className="w-25">
                <input className="form-control" type="search" placeholder="Search" aria-label="Search" />
              </div>

            </div>
          </nav>
        </div>
      </section>
    )
  }
}

export default Navbar
