
import React, { Component } from 'react';
import axios from 'axios';
import Joi from 'joi';
import JwtDecode from 'jwt-decode';
import SecureLS from 'secure-ls';
import Authentication from '../authentication'

var ls = new SecureLS({encodingType: 'aes'});

class Login extends Component {
  state = {
    email: '',
    password: '',
    error: '',
  }

  loginFormChange = (e) => {
    let state = {...this.state};
    state[e.target.name] = e.target.value;
    this.setState(state);
  }
  loginFormSubmit = (e) => {
    e.preventDefault();

    let validationResponse = this.loginFormValidation();

    if(validationResponse.error) {
      // Validation Error
      let state = {...this.state};
      state.error = validationResponse.error;
      this.setState(state);
    } else{
      this.loginDataSend();
    }

  }
  loginFormValidation = () => {
    let schema = Joi.object({
      email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
      password: Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')).required(),
    });
    let state = {...this.state};
    delete state.error;
    return schema.validate(state, {abortEarly: false});
  }
  loginDataSend = async () => {
    let state = {...this.state};
    delete state.error;
    let {data} = await axios.post('https://route-egypt-api.herokuapp.com/signin', state);

    if(data.message === 'success') {
      let stateS = {...this.state};
      // save decoded token to localStorage
      ls.set('currentUser', data.token);
      // remove errors from state
      stateS.error = '';
      this.setState(stateS);
      // redirect to homePage
      Authentication.logIn(()=>{
        this.props.history.replace('/home');
      });
    } else{
      console.log(data.message);
      let stateF = {...this.state};
      stateF.error = 'error';
      this.setState(stateF);
    }
  }


  render() {
    return (
      <section>
        <div className="container">
          <h2>Login</h2>

          {
            this.state.error !== '' ?
              <ul className="alert alert-danger list-unstyled mt-3">
                <h5>Error!</h5><hr/>
                <li>Incorrect Email or Password, Please try again.</li>
              </ul> : null
          }

          <form onSubmit={this.loginFormSubmit} className="my-4 p-5 border border-white-50 rounded">
            <label htmlFor="" className="mt-3">Email:</label>
            <input type="text" className="form-control" name="email" onChange={this.loginFormChange} value={this.state.email} />

            <label htmlFor="" className="mt-3">Password:</label>
            <input type="password" className="form-control" name="password" onChange={this.loginFormChange} value={this.state.password} />

            <button type="submit" className="btn btn-outline-warning mt-4">Login</button>
          </form>

        </div>
      </section>
    )
  }
}
export default Login
