
import React, { Component } from 'react'
import { Link } from 'react-router-dom';

class Footer extends Component {
  render() {
    return (
      <section className="bg-black-25 mt-5 py-4">
        <div className="container-fluid">
          <div className="row">

            <div className="col-12 text-center">
              <Link className="h4 text-warning" to="/home"><strong><img className="mr-2 mb-1" src="https://img.icons8.com/ios-glyphs/20/ffc107/film-reel--v1.png" alt="logo"/>MyMDB_</strong></Link>
            </div>

          </div>
        </div>
      </section>
    )
  }
}
export default Footer
