import React, { Component } from 'react'
import axios from 'axios';

class Home extends Component {


  state = {
    movies:[],
    tv:[],
  }

  getTrending = async (mediaType) => {
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=e38939d40dcf4625737c388e54bc8f1f`);
    this.setState({[mediaType]:data.results});
  }

  componentDidMount() {
    this.getTrending('movies');
    this.getTrending('tv');
  }

  render() {

    return (

      <section>
        <div className="container-fluid">
          <div className="row">

            <div className="col-md-4">
              <div className="movie my-3 p-5">
                <div className="brdr w-25 mb-4"></div>
                <h2 className="text-uppercase">Trending<br/><span className="text-big text-warning">Movies</span></h2>
                <h3 className="h4">To watch right now</h3>
                <p>Most watched movies by days</p>
                <div className="brdr w-75 mt-4"></div>
              </div>
            </div>

            {this.state.movies.slice(0,10).map((movie)=>
              <div key={movie.id} className="col-6 col-md-2">
                <div className="movie my-3">
                  <img className="w-100 rounded" src={'https://image.tmdb.org/t/p/w500/'+movie.poster_path} alt=""/>
                  <h3 className="h5 mt-2">{ movie.title===undefined ? movie.original_name : movie.title }</h3>
                </div>
              </div>
            )}

          </div>
          <div className="row">

            <div className="col-md-4">
              <div className="movie my-3 p-5">
                <div className="brdr w-25 mb-4"></div>
                <h2 className="text-uppercase">Trending<br/><span className="text-big text-warning">TV Shows</span></h2>
                <h3 className="h4">To watch right now</h3>
                <p>Most watched TV Shows by days</p>
                <div className="brdr w-75 mt-4"></div>
              </div>
            </div>

            {this.state.tv.slice(0,10).map((tvShow)=>
              <div key={tvShow.id} className="col-6 col-md-2">
                <div className="tvShow my-3">
                  <img className="w-100 rounded" src={'https://image.tmdb.org/t/p/w500/'+tvShow.poster_path} alt=""/>
                  <h3 className="h5 mt-2">{ tvShow.title===undefined ? tvShow.original_name : tvShow.title }</h3>
                </div>
              </div>
            )}

          </div>
        </div>
      </section>

    )
  }
}
export default Home
