
import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import Img404 from '../imgs/404.svg';

class NotFound extends Component {
  render() {
    return (
      <section id="S404" className="">
        <div className="container-fluid">
          <div className="404 d-flex flex-column justify-content-center align-items-center">
            <img className="img-404" src={Img404} alt="Not Found Page"/>
            <Link className="btn btn-outline-warning" to="/home">Back to Home</Link>
          </div>
        </div>
      </section>
    )
  }
}
export default NotFound
