
import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios';
import Joi from 'joi';

class Register extends Component {
  state = {
    first_name:'',
    last_name:'',
    email:'',
    password:'',
    age:'',
    error: '',
    success: '',
  }
  registerFormChange = (e) => {
    let state = {...this.state};
    state[e.target.name] = e.target.value;
    this.setState(state);
  }
  registerFormSubmit = (e) => {
    e.preventDefault();

    let validationResponse = this.registerFormValidation();

    if(validationResponse.error) {
      // Validation Error
      let state = {...this.state};
      state.error = validationResponse.error;
      this.setState(state);
    } else{
      this.registerDataSend();
    }

  }
  registerFormValidation = () => {
    let schema = Joi.object({
      first_name: Joi.string().min(3).max(12).required(),
      last_name: Joi.string().min(3).max(12).required(),
      email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
      age: Joi.number().integer().min(10).max(80).required(),
      password: Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')).required(),
    });
    let state = {...this.state};
    delete state.error;
    delete state.success;
    return schema.validate(state, {abortEarly: false});
  }
  registerDataSend = async () => {
    let state = {...this.state};
    delete state.error;
    delete state.success;
    let {data} = await axios.post('https://route-egypt-api.herokuapp.com/signup', state);

    if(data.message === 'success') {
      let stateS = {...this.state};
      stateS.error = '';
      stateS.success = 'Registration Success';
      this.setState(stateS);
    } else{
      console.log(data.message);
      let stateF = {...this.state};
      stateF.error = { details: [ {message: data.message,} ] }
      this.setState(stateF);
    }
  }

  render() {
    return (
      <section>
        <div className="container">
          <h2>Register</h2>

          {
            this.state.error !== '' ?
              <ul className="alert alert-danger list-unstyled mt-3">
                <h5>Error!</h5><hr/>
                {this.state.error.details.map((index)=> <li key={index.message}>{'- '+index.message}</li>)}
              </ul> : null
          }

          {
            this.state.success == 'Registration Success' ?
              <ul className="alert alert-success list-unstyled mt-3">
                <h5>Success</h5><hr/>
                <li>Registration Success, Go to <Link to="/login">Login</Link>.</li>
              </ul> : null
          }

          <form onSubmit={this.registerFormSubmit} className="my-4 p-5 border border-white-50 rounded">
            <label htmlFor="">First Name:</label>
            <input type="text" className="form-control" name="first_name" onChange={this.registerFormChange} value={this.state.first_name} />

            <label htmlFor="" className="mt-3">Last Name:</label>
            <input type="text" className="form-control" name="last_name" onChange={this.registerFormChange} value={this.state.last_name} />

            <label htmlFor="" className="mt-3">Email:</label>
            <input type="text" className="form-control" name="email" onChange={this.registerFormChange} value={this.state.email} />

            <label htmlFor="" className="mt-3">Age:</label>
            <input type="number" className="form-control" name="age" onChange={this.registerFormChange} value={this.state.age} />

            <label htmlFor="" className="mt-3">Password:</label>
            <input type="password" className="form-control" name="password" onChange={this.registerFormChange} value={this.state.password} />

            <button type="submit" className="btn btn-outline-warning mt-4">Create Account</button>
          </form>
        </div>
      </section>
    )
  }
}
export default Register
